import { Parser } from 'im.v2.lib.parser';

import { BaseMessage } from 'im.v2.component.message.base';
import { MessageStatus } from 'im.v2.component.message.elements';

import type { ImModelMessage } from 'im.v2.model';

// @vue/component
export const SmileMessage = {
	name: 'MediaMessage',
	components: {
		BaseMessage,
		MessageStatus,
	},
	props: {
		item: {
			type: Object,
			required: true,
		},
		dialogId: {
			type: String,
			required: true,
		},
	},
	emits: ['contextMenuClick', 'quoteMessage'],
	computed:
	{
		message(): ImModelMessage
		{
			return this.item;
		},
		formattedText(): string
		{
			return Parser.decodeMessage(this.message);
		},
	},
	template: `
		<BaseMessage
			:dialogId="dialogId"
			:item="item"
			:withMessageStatus="false"
			:withText="false"
			:withBackground="false"
			:withReactions="true"
			reactionsSelectorSlot="content"
			@contextMenuClick="$emit('contextMenuClick', $event)"
			@quoteMessage="$emit('quoteMessage', $event)"
		>
			<div class="bx-im-message-base__text --emoji" v-html="formattedText"></div>
			<MessageStatus :item="message" :isOverlay="true" />
		</BaseMessage>
	`,
};
